.transaction-history {
    width: 300px;
        margin-left: auto;
        margin-right: auto;
        box-shadow: rgb(0 0 0 / 20%) 0px 3px 3px -2px, rgb(0 0 0 / 14%) 0px 3px 4px 0px,
                rgb(0 0 0 / 12%) 0px 1px 8px 0px;
}

thead {
    text-transform: uppercase;
    font-size: 10px;
    background-color: rgb(75, 170, 207);
    color: var(--white);
}

tbody {
color: var(--gray);
}

th, td {
    width: calc(100%/3);
    padding: 10px;
}

td {
    background-color: var(--white);
}

.gray {
    background-color: var(--light-gray);
}

.type {
    text-transform: capitalize;
}

.amount {
text-align: right;
}

.currency {
text-align: center;
}